*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Sen', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: $color-text;

  // @include respond(tab-port) {
  //   padding: 0;
  // }
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1 0 auto;
  max-width: 1170px;
  margin: 0 auto;
}

.link {
  &,
  &:link,
  &:visited {
    text-decoration: none;
    cursor: pointer;
  }

  &:hover {
    color: $color-hover;
  }
}

.input {
  width: 100%;
  height: 45px;
  padding: 12px 20px 14px;
  border: 2px solid white;
  border-radius: 25px;

  &:focus {
    outline: none;
    border: 2px solid $color-secondary;
  }
}

.input::placeholder {
  font-family: 'Sen', sans-serif;
  font-size: 17px;
  line-height: 20px;
}
